import {
    INCREMENT_COUNTER,
    DECREMENT_COUNTER,
    REFRESH_ING,
    LOADING_ING,
    GET_ALL_TRENDS,
    LOAD_TRENDS,
} from '../actions/ActionsTypes'

import {ListView} from 'antd-mobile'

const initState = {
    dataSource: new ListView.DataSource({
        rowHasChanged: (row1, row2) => row1 !== row2,
    }),
    refreshing: false,
    loading: false,
    //
    pageSize : 1,                  // 
    size : 1,                  // 
    maxPage: 1,              // 
    canLoad: false,
    rows:[
        {
            name:"df",
            content: "23fffddfgdfdgfdghgf",
            type: 1,
            thumbsups: [
                {
                    name: "djy"
                },
                {
                    name: "djy2"
                },
                {
                    name: "djy3"
                }
            ],
            comment: [
                {
                    name: "djy",
                    contect: "",
                },
                {
                    name: "djyerew",
                    contect: "",
                },

            ],
        },
    ],
    }

export default function dongtai(state = initState, action) {
    let total, rows, pageSize, maxPage, canLoad;

    switch (action.type) {
        case REFRESH_ING:
            return Object.assign({}, state, {refreshing : true});
            break;
        case LOADING_ING:
            return Object.assign({}, state, {loading : true});
            break;
        case GET_ALL_TRENDS:
            return Object.assign({}, state, action.res);
            break;

        case LOAD_TRENDS:
            return Object.assign({}, state, action.res);
            break;

        default:
            return state;
    }
}


